<template>
  <div>
    <div class="chengkai"></div>
    <div class="title">
      <span style="font-size:20px;color:#8f8f8f;margin-left:20px">-</span>
    </div>
    <div class="sale_date">
      <div class="date">
        <!--  -->
        <div class="date_search">
          <span class="search_title" style="font-size:18px;color:#000;">:</span>
          <!--  -->
          <div class="search_list clearfix">
            <el-radio-group fill="rgba(248, 164, 39, 1)" v-model="radioVal">
              <el-radio-button label=""></el-radio-button>
              <el-radio-button label="7"></el-radio-button>
              <el-radio-button label="30"></el-radio-button>
              <el-radio-button label=""></el-radio-button>
              <el-radio-button label=""></el-radio-button>
              <el-radio-button label=""></el-radio-button>
            </el-radio-group>
          </div>
          <!--  -->
          <transition name="dateshow">
            <div class="choose_date" v-show="showDate">
              <el-date-picker v-model="setDateVal" type="daterange" range-separator="" start-placeholder="" end-placeholder="" value-format="yyyy-MM-dd">
              </el-date-picker>
            </div>
          </transition>
          <!--  -->
          <div class="submit_search">
            <button @click="submitSearch()"></button>
          </div>
        </div>
        <!--  -->
        <div class="date_overview">
          <div>
            <span style="font-size:18px;color:#000;font-weight:600 "></span>
            <span v-text="overview.time"></span>
          </div>
          <div class="overview_detail">
            <ul class="clearfix">
              <li>
                <p></p>
                <p v-text="overview.cateNumber"></p>
              </li>
              <li>
                <p></p>
                <p v-text="overview.totalNumber"></p>
              </li>
              <li>
                <p></p>
                <p v-text="overview.totalSales"></p>
              </li>
              <li>
                <p></p>
                <p v-text="overview.averageProNumber"></p>
              </li>
              <li>
                <p></p>
                <p v-text="overview.averageSales"></p>
              </li>
            </ul>
          </div>
        </div>

        <!--  -->
        <div class="date_prolist">
          <h2 style="font-size:18px;color:#000;margin-bottom:10px"></h2>
          <!-- <div class="export_excel">excel</div> -->
          <el-table :data="tableData" stripe style="width: 100%" ref="exportTable">
            <el-table-column prop="id" label="" align="center">
            </el-table-column>
            <el-table-column prop="channel" label="" align="center">
            </el-table-column>
            <el-table-column prop="listNum" label="" align="center">
            </el-table-column>
            <el-table-column prop="date" label="" align="center">
            </el-table-column>
            <el-table-column prop="category" label="" align="center">
            </el-table-column>
            <el-table-column prop="proNumber" label="" align="center">
            </el-table-column>
            <el-table-column prop="price" label="" align="center">
            </el-table-column>
          </el-table>
          <el-pagination background layout="prev, pager, next" :total="1000">
          </el-pagination>
        </div>
        <!--  -->
        <div class="date_sale_compare">
          <h2 style="font-size:18px;color:#000;margin-bottom:10px"></h2>
          <div class="charts" ref="myChart"></div>
        </div>
      </div>
    </div>
  </div>

</template>
<style>
@import url("../../assets/css/saleManage/saleDate.css");
</style>
<script>
export default {
