    return {
      radioVal: "", //
      tableData: [
        {
          id: "1",
          channel: "",
          listNum: "xxx-xxx",
          date: "2018-9-21",
          category: "2",
          proNumber: "20",
          price: "US$1192"
        },
        {
          id: "1",
          channel: "",
          listNum: "xxx-xxx",
          date: "2018-9-21",
          category: "2",
          proNumber: "20",
          price: "US$1192"
        },
        {
          id: "1",
          channel: "",
          listNum: "xxx-xxx",
          date: "2018-9-21",
          category: "2",
          proNumber: "20",
          price: "US$1192"
        }
      ], //
      setDateVal: "", //
      showDate: false, //
      overview: {
        time: "2018-10-1 00:00-9:00",
        cateNumber: "1", //
        totalNumber: "2", //
        totalSales: "US$111", //
        averageProNumber: "4", //
        averageSales: "US$12" //
      }, //
      startDate: "",
      endDate: ""
    };
  },
  methods: {
    //
    getStartAndEnd() {
      this.endDate = this.getTheDate();
      switch (this.radioVal) {
        case "":
          this.startDate = this.getTheDate();
          console.log(this.startDate, this.endDate);
          break;
        case "7":
          this.startDate = this.getTheDate(-7);
          console.log(this.startDate, this.endDate);
          break;
        case "30":
          this.startDate = this.getTheDate(-30);
          console.log(this.startDate, this.endDate);
          break;
        case "":
          this.startDate = this.getTheDate(-182);
          console.log(this.startDate, this.endDate);
          break;
        case "":
          this.startDate = this.getTheDate(-365);
          console.log(this.startDate, this.endDate);
          break;
        case "":
          this.startDate = this.setDateVal[0];
          this.endDate = this.setDateVal[1];
          console.log(this.startDate, this.endDate);
          break;
      }
    },
    //
    submitSearch: function() {
      this.getStartAndEnd();
      //
      var that = this;
    
      this.$axios
        .get("https://okyoo.xwetech.com/api/v1.0/sales/general_data")
        .then(res => {
          console.log(res);
        });
    },

    //
    setCharts: function() {
      var startDate = this.getTheDate(-365);
      var endDate = this.getTheDate();
      var thisYearData, lastYearData;
      //
      // $.ajax({
      //   url: '',
      //   type: 'get',
      //   data: {
      //     startDate: startDate
      //   },
      //   success: function (info) {
      //     thisYearData = info.result;
      //     //
      //     $.ajax({
      //       url: '',
      //       type: 'get',
      //       data: {
      //         endDate: endDate
      //       },
      //       success: function (info) {
      //         lastYearData = info.result;
      //         //
      //         var myChart = echarts.init(this.$refs.myChart);
      //         var option = {
      //           grid: {
      //             left: '3%',
      //             right: '4%',
      //             top: '18%',
      //             bottom: '3%',
      //             containLabel: true
      //           },
      //           legend: {
      //             data: ['', '']
      //           },
      //           tooltip: {
      //             trigger: 'item',
      //           },
      //           xAxis: {
      //             type: 'category',
      //             name: '',
      //             data: ['', '', '', '', '', '', '', '', '', '', '', ''],
      //           },
      //           yAxis: {
      //             type: 'value',
      //             name: 'US$',
      //             splitLine: {
      //               show: false
      //             },
      //           },
      //           series: [
      //             {
      //               name: '',
      //               type: 'line',
      //               data: thisYearData
      //             },
      //             {
      //               name: '',
      //               type: 'line',
      //               data: lastYearData
      //             }
      //           ]
      //         };
      //         myChart.setOption(option)
      //       }
      //     })
      //   }
      // })
    },
    //
    getTheDate(day) {
      day = day || 0;
      var nowDate = new Date();
      var targetday = nowDate.getTime() + 1000 * 60 * 60 * 24 * day;
      nowDate.setTime(targetday);
      var nowYear = nowDate.getFullYear();
      var nowMonth =
        nowDate.getMonth() + 1 >= 10
          ? nowDate.getMonth() + 1
          : "0" + (nowDate.getMonth() + 1);
      var nowDay =
        nowDate.getDate() >= 10 ? nowDate.getDate() : "0" + nowDate.getDate();
      nowDate = nowYear + "-" + nowMonth + "-" + nowDay;
      return nowDate;
    }
  },
  watch: {
    radioVal: function(nv) {
      if (nv == "") {
        this.showDate = true;
      } else {
        this.showDate = false;
        this.setDateVal = "";
      }
    }
  },
  mounted: function() {
    // 
    var that = this;
    console.log("");
    //
    this.submitSearch(); //
    this.setCharts(); //
    window.onresize = function() {
      that.$echarts.init(that.$refs.myChart).resize();
    };
  }
};
</script>




