<template>
  <div>
    <div class="chengkai"></div>
    <div class="title">
      <span style="font-size:20px;color:#8f8f8f;margin-left:20px">-</span>
    </div>
    <div class="sale_channel">
      <div class="channel">
        <!--  -->
        <div class="channel_search">
          <span class="search_title" style="font-size:18px;color:#000;">:</span>
          <!--  -->
          <div class="search_list clearfix">
            <el-radio-group fill="rgba(248, 164, 39, 1)" v-model="radioVal">
              <el-radio-button label=""></el-radio-button>
              <el-radio-button label="Amazon"></el-radio-button>
              <el-radio-button label="Wayfair"></el-radio-button>
              <el-radio-button label="Walmart"></el-radio-button>
              <el-radio-button label="eBay"></el-radio-button>
              <el-radio-button label="Okyoo"></el-radio-button>
              <el-radio-button label="Target"></el-radio-button>
              <el-radio-button label="Overstock"></el-radio-button>
              <el-radio-button label="Houzz"></el-radio-button>
              <el-radio-button label=""></el-radio-button>
            </el-radio-group>
          </div>

          <!--  -->
          <div class="submit_search">
            <button @click="submitSearch()"></button>
          </div>
        </div>
        <!--  -->
        <div class="channel_overview">
          <div>
            <span style="font-size:18px;color:#000;font-weight:600 "></span>
            <span>30</span>
          </div>
          <div class="overview_charts" ref="saleCharts">
          </div>
        </div>
        <!--  -->
        <div class="channel_sale_trend">
          <div>
            <span style="font-size:18px;color:#000;font-weight:600 "></span>
            <span>30</span>
          </div>
          <div class="trend_charts" ref="trendCharts">
          </div>
        </div>

        <!--  -->
        <div class="channel_prolist">
          <h2 style="font-size:18px;color:#000;margin-bottom:10px"></h2>
          <!-- <div class="export_excel">excel</div> -->
          <el-table :data="tableData" stripe style="width: 100%" ref="exportTable">
            <el-table-column prop="id" label="" align="center">
            </el-table-column>
            <el-table-column prop="channel" label="" align="center">
            </el-table-column>
            <el-table-column prop="listNum" label="" align="center">
            </el-table-column>
            <el-table-column prop="date" label="" align="center">
            </el-table-column>
            <el-table-column prop="category" label="" align="center">
            </el-table-column>
            <el-table-column prop="proNumber" label="" align="center">
            </el-table-column>
            <el-table-column prop="price" label="" align="center">
            </el-table-column>
          </el-table>
          <el-pagination background layout="prev, pager, next" :total="1000">
          </el-pagination>
        </div>

      </div>
    </div>
  </div>
</template>
<style>
@import url("../../assets/css/saleManage/saleChannel.css");
</style>

<script>
export default {
  data: function() {
    return {
      radioVal: "", //
      tableData: [
        {
          id: "1",
          channel: "",
          listNum: "xxx-xxx",
          date: "2018-9-21",
          category: "2",
          proNumber: "20",
          price: "US$1192"
        },
        {
          id: "1",
          channel: "",
          listNum: "xxx-xxx",
          date: "2018-9-21",
          category: "2",
          proNumber: "20",
          price: "US$1192"
        },
        {
          id: "1",
          channel: "",
          listNum: "xxx-xxx",
          date: "2018-9-21",
          category: "2",
          proNumber: "20",
          price: "US$1192"
        }
      ] //
    };
  },
  methods: {
    //
    submitSearch: function() {
      //
      var that = this;
      var data = {};
      this.$axios
        .get("https://okyoo.xwetech.com/api/v1.0/sales/general_data")
        .then(res => {
          console.log(res);
        });
    },

    //
    setSaleCharts() {
      var mySaleCharts = this.$echarts.init(this.$refs.saleCharts);
      var option = {
        color: ["rgba(0, 204, 255, 1)"],
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 
            type: "shadow" // 'line' | 'shadow'
          }
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: [
          {
            type: "category",
            data: [
              "Amazon",
              "Wayfair",
              "Walmart",
              "eBay",
              "Okyoo",
              "Target",
              "Overstock",
              "Houzz",
              "others"
            ],
            axisTick: {
              alignWithLabel: true
            }
          }
        ],
        yAxis: [
          {
            type: "value"
          }
        ],
        series: [
          {
            name: "",
            type: "bar",
            barWidth: "60%",
            data: [10, 52, 200, 334, 390, 330, 220, 122, 400]
          }
        ]
      };
      mySaleCharts.setOption(option);
    },
    //
    setTrendCharts() {
      var myTrendCharts = this.$echarts.init(this.$refs.trendCharts);
      var option = {
        color:["rgba(253, 171, 89, 1)"],
        xAxis: {
          type: "category",
          data: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"]
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            data: [820, 932, 901, 934, 1290, 1330, 1320],
            type: "line",
            smooth: true
          }
        ]
      };
      myTrendCharts.setOption(option);
    }
  },
  watch: {
    radioVal: function(nv) {
      if (nv == "") {
      } else {
      }
    }
  },
  mounted: function() {
    // 
    var that = this;
    console.log("");
    //
    this.setSaleCharts();
    this.setTrendCharts();
  }
};
</script>



