<template>
    <div>
        <div class="chengkai"></div>
        <div class="title">
            <span style="font-size:20px;color:#8f8f8f;margin-left:20px"></span>
        </div>
        <div class="new_pro_dev">
            <!--  -->
            <div class="search">
                <h3 style="margin-left:30px;margin-top:10px">:</h3>

                <div class="search_dv">
                    <span> x  x </span><input type="number" v-model="searchData.width"> x <input type="number" v-model="searchData.length"> x <input type="number" v-model="searchData.height">
                </div>
                <div class="search_dv">
                    <span style="margin-left:42px">$USD:</span>
                    <input type="number" v-model="searchData.fob">
                </div>
                <div class="search_dv">
                    <span style="margin-left:60px">lbs:</span>
                    <input type="number" v-model="searchData.weight">
                </div>
                <div class="search_dv">
                    <el-select clearable placeholder="" filterable v-model="searchData.color_filter">
                        <el-option v-for="(item, index) in selectData.color_filter" :key="index" :value="item">
                        </el-option>
                    </el-select>
                    <el-select clearable placeholder="" filterable v-model="searchData.material_filter">
                        <el-option v-for="(item, index) in selectData.material_filter" :key="index" :value="item">
                        </el-option>
                    </el-select>
                    <el-select clearable placeholder="" filterable v-model="searchData.feature_filter">
                        <el-option v-for="(item, index) in selectData.feature_filter" :key="index" :value="item">
                        </el-option>
                    </el-select>
                    <el-select clearable placeholder="" filterable v-model="searchData.category_filter">
                        <el-option v-for="(item, index) in selectData.category_filter" :key="index" :value="item">
                        </el-option>
                    </el-select>
                </div>
                <div class="submitBtn">
                    <button @click="submitSearchData()"></button>
                </div>
            </div>
            <!--  -->
            <!--  -->
            <div class="proList">
                <el-table :data="tableData" style="width: 100%">
                    <el-table-column align='center' label="" prop="id">
                    </el-table-column>
                    <el-table-column align='center' label="">
                        <template slot-scope="props">
                            <img style="width:50px" :src="props.row.image" alt="">
                        </template>
                    </el-table-column>
                    <el-table-column align='center' label="" prop="channel">
                    </el-table-column>
                    <el-table-column align='center' label="" prop="price">
                    </el-table-column>
                    <el-table-column align='center' label="">
                        <template slot-scope="props">
                            <span>{{props.row.estimate_profit|keepTwoPoint1}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column align='center' label="" prop="estimate_profit_margin">
                         <template slot-scope="props">
                            <span>{{props.row.estimate_profit_margin|keepTwoPoint2}}</span>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
    </div>

</template>
<style>
@import url("../../assets/css/proDev/newProDev.css");
</style>
<script>
export default {
  data() {
    return {
      baseUrl: "",
      searchData: {
        width: "",
        height: "",
        length: "",
        fob: "",
        weight: "",
        color_filter: "",
        material_filter: "",
        feature_filter: "",
        category_filter: ""
      }, //
      selectData: {
        color_filter: [],
        material_filter: [],
        feature_filter: [],
        category_filter: []
      }, //
      tableData: [] //
    };
  },
  methods: {
    //
    submitSearchData() {
      var that = this;
      //
      this.$axios
        .get("/api/v1.0/newpro_dev/pro_list", {
          params: that.searchData
        })
        .then(res => {
            console.log(res);
          this.tableData = res.data.result;
        });
    }
  },
  mounted() {
    //
    this.$axios
      .get("/api/v1.0/newpro_dev/search_select")
      .then(res => {
        //   console.log(res.data);
        this.selectData.color_filter = res.data.result.color_filter;
        this.selectData.material_filter = res.data.result.material_filter;
        this.selectData.feature_filter = res.data.result.feature_filter;
        this.selectData.category_filter = res.data.result.category_filter;
      });
  },
  filters:{
      keepTwoPoint1(val){
          return 'US$'+val.toFixed(2);
      },
       keepTwoPoint2(val){
          return val.toFixed(2)+'%';
      }
  }
};
</script>


